.signUpContainer{
    height: 100vh;
    margin-top: 100px;
}
.signUpSecondContainer{
    width: 50%;
    margin: auto;
    text-align: center;
    border: 0.3px solid #ccc;
    padding: 30px;
    box-shadow: 0px 0px 16px #ccc;
}
.profileSetting{
    height: 30%;
}
.profileSettingButton{
    text-align: center;
    margin-top: 20px;
}
.signUpButton{
    align-items: center;
    margin-top: 2rem;
    color: aliceblue;
    padding: 5px;
    background-color: blue;
    font-size: 2rem;
}
.signUpButton:hover{
    background-color: rgb(3, 3, 77);
    color: white;
    transition: ease-in 0.5s;
}
.backToLogin{
    font-size: 20px;
}