.myProfileContainers{
    display: flex;
}
.editIcon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    margin-left: 50px;
    font-size: 25px;
}
.editIcon ul{
    list-style: circle;
}
.editIcon li{
    margin-bottom: 20px;
    text-decoration: underline;
}
.editContainer{
    display: block;
    font-weight: bold;
}

.goToPost{
    /*width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: blue;*/
}
.postIcon{
    font-size: 20px;
}
.myProfileContainer{
    margin: 50px auto;
    width: 50%;
    border: 5px solid #ccc;
    margin-left: 100px;
}
.myProfileImage{
    margin: 0 auto;
    width: 50%;
    height: 30vh;
    border-radius: 50%;
    background-color: #000;
}
.myProfileImage img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.myProfileInformation{
    display: flex;
    margin-left: 30px;
}
.myProfileInfoContainer{
    display: flex;
    width: 80%;
}
.myProfileUsername{
    font-size: 30px;
    margin-right: 15px;
}
.myProfileAge{
    font-size: 30px;
    margin-right: 15px;
}
.myProfileNationallity{
    font-size: 30px;
    margin-top: 30px;
    margin-right: 15px;
}
.myProfileOnlineStatus{
    font-size: 30px;
    margin-right: 15px;
}
.myProfileLanguages{
    margin-left: 30px;
    width: 80%;
}
.myProfileDescription{
    overflow-y: scroll;
    width: 80%;
    padding: 0px 30px;
}
.myProfileLanguages p{
    font-size: 20px;
    font-weight: bold;
    border-bottom: 5px solid #ccc;
}
.myProfileDescriptionTitle{
    font-size: 20px;
    font-weight: bold;
    border-bottom: 5px solid #ccc;
}
.myProfileUserdescription{
    font-size: 15px;
}
.userProfileTheme{
    text-align: center;
    margin: 20px;
    color: navy;
    font-size: 2rem;
    font-weight: bolder;
}