.container{
  width: 100%;
}
.informationContainer{
  height: 300px;
  margin: auto;
  color: black;
}
.pictureContainer{
  min-height: 35vh;
  padding-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.username p{
  text-align: center;
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 2px;
}
.homeItems{
  margin-top: 40px;
}
.homeIconBack{
  height: auto;
  width: 50px;
  padding-left: 80px;
}
.homeIcon{
  padding-top: 15px;
  height: 20px;
  width: 40px;
  font-size: 20px;
}
.homeItem{
  height: 55px;
  display: flex;
  padding-left: 100px;
  border: 0.1px solid black;
  border-left: none;
  border-right: none;
}
.homeItem:hover{
    color: blue;
}
.homeItemName{
  width: 100%;
  padding-left: 60px;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: 0.8px;
}

