.footer{
    bottom: 5%;
    width:100%;
    height: 3.5rem;
    background-color: rgb(3, 3, 75);
    color: white;
    display: flex;
    flex-direction: row-reverse;
}
.footerContainer{
    margin-left: 20px;
}
.footerTitle{
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 60px;
    font-size: 18px;
}
.footerTitle:hover{
    background-color: rgb(14, 3, 128);
    transition: ease-in 0.5s;
}
.footerContent{
    margin: auto;
}
.footerIcon{
    font-size: 30px;
    color: aliceblue;
}

