.policyContainer{
    display: flex;
    justify-content: center;
    margin: 100px auto;
    height: 60vh;
    width: 100%;
}
.policyStyles{
    width: 60%;
    box-shadow: 0px 0px 5px black;
    border: 2px solid black;
}
.policyStyles header{
    border-bottom: 2px solid black;
    color: navy;
}
.policyStyles ul{
    list-style: circle;
}
.policyStyles li{
    font-size: 1.3rem;
    margin-bottom: 10px;
}