.profileEditContainer{
    text-align: center;
    margin: 100px auto;
    align-items: center;
}
.profileEditContainer .updateTitle{
    font-size: 2.5rem;
    color: navy;
}
.profileEditSecondContainer{
    margin:50px auto;
    align-items: center;
    justify-content: center;
    width: 40%;
    padding: 50px 50px;
    border: 5px solid #ccc;
    box-shadow: 0px 0px 10px black;
}
.selectedButton{
    align-items: center;
    padding: 15px 15px;
}
.selectedButton button{
    padding: 5px 5px;
    margin: 10px 10px;
}
.contactusButton{
    align-items: center;
    text-align: center;
    margin-top: 2rem;
    color: aliceblue;
    padding: 5px;
    background-color: blue;
    font-size: 2rem;
}
.contactusButton:hover{
    background-color: rgb(3, 3, 77);
    color: white;
    transition: ease-in 0.5s;
}