.container{
    height: 100%;
    width: 100%;
}
.position{
    text-align: center;
}
.theme{
    color: navy;
    height: 30vh;
}
.news{
    font-size: 2.5rem;
}
.select{
    font-size: 2rem;
}
.AiFillCaretDown{
    font-size: 2rem;
}
.AiFillCaretDown:hover{
    font-size: 2.5rem;
}
.itemsContainer{
    width: auto;
    margin: auto;
}
ul{
    list-style: none;
}
.items li{
    padding: 30px;
}
.items li:hover{
    transition: ease-in 0.5s;
    transform: translateY(-10px)
}
.items{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px 30px;
    font-size: 2.5rem;
}