.myPosts{
    width: 80%;
    margin: auto;
}
.myPostsTheme{
    text-align: center;
    font-size: 2.5rem;
    font-weight: bold;
    color: navy;
}
.myPostsContainer{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
    grid-row-gap: 6px;
    grid-column-gap: 6px;
    padding: 0px;
    margin: 0;
    list-style-type: none;
}
.myPostsOutline{
    height: 300px;
    margin: 10px 30px;
    overflow-y: scroll;
    border: 3px solid #ccc;
    padding: 10px 10px;
    box-shadow: 0px 0px 2px black;
}
.myPostsHeader{
    display: flex;
    margin-left: 30px;
}
.myPostsHeader p{
    margin-left: 10px;
    font-weight: bold;
    font-size: 20px;
}
.myPostsImageContainer{
    width: 100%;
}
.myPostsImage{
    height: 100px;
    width: 100%;
}