.userListsContainer{
    width: 100%;
    margin: 1px;
    border-bottom: 2px solid #ccc;
}
.userListsInfo{
    display: flex;
}
.userListsImage{
    width: 30%;
    object-fit: cover;
}
.userListsImage img{
    width: 100%;
    min-height: 100%;
}
.userListsUserInfos{
    width: 70%;
}
.userListsBasicInfo{
    display: flex;
    align-items: center;
    padding-left: 50px;
    border-bottom: 1px solid #ccc;
}
.userListsBasicInfoUsername{
    font-size: 2rem;
}
.userListsBasicInfoAge{
    font-size: 2rem;
}
.userListsBasicInfoStatus{
    font-size: 1rem;
    font-weight: 300;
}
.userListUserLanguages{
    display: flex;
    padding-left: 50px;
    border-bottom: 1px solid #ccc;
}
.userListsUserDescription{
    padding-left: 50px;
}