.friendsChatContainer{
    display: flex;
    margin: 100px auto;
    justify-content: center;
    width: 70%;
    height: 80vh;
    border: 2px solid #ccc;
    box-shadow: 0px 0px 5px black;
    overflow-y: scroll;
}
.friendsChatLists{
    flex: 0.5;
    border-right: 2px solid black;
    border-left: 2px solid black;
}
.friendsChats{
    position: relative;
    flex: 0.6;
    height: 100%;
    width: 100%;
}
.friendsChatHeader{
    width: 100%;
    height: 40px;
    text-align: center;
    font-weight: bolder;
    font-size: large;
    color: aliceblue;
    background-color: navy;
}
.friendsChatInfo{
    display: flex;
    overflow-x: hidden;
    font-size: 1.5rem;
    width: 100%;
    border-bottom: 1px solid black;
}
.friendsChatInfo p{
    margin-left: 20px;
}
.friendsChatsInput{
    position: absolute;
    bottom: 10px;
    right: 10%;
    display: flex;
}
.friendsChats input{
    width: 200px;
}
.friendsChatsButton{
    height: 100%;
    padding: 2px 2px;
    border: 1px solid black;
}