.themeHeader{
    text-align: center;
    font-size: 2.5rem;
    font-weight: bold;
    color: navy;
}
.newsContainer{
    width: 70%;
    margin: auto;
    background-color: rgb(245, 252, 252);
    border: 2px solid #ccc;
    box-shadow: 0px 0px 10px black;
}
.newsItemContainer{
    height: 100px;
}
.gotoOtherLanguages{
    margin: auto;
}
.gotoOtherLanguages ul{
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gotoOtherLanguages li{
    font-size: 20px;
    padding: 10px;
}
.newsStyle{
    height: 170px;
}
.newsTitle{
    margin-left: 30px;
}
.newsTheme{
    font-weight: bold;
}
.newsDescription{
    font-size: 13px;
}
.newsPosition{
    display: flex;
}
.goToNews{
    font-size: 20px;
    letter-spacing: 1px;
}
.fetchingDatas{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
    font-size: 30px;
}
.AiOutlineRedo{
    font-size: 40px;
    margin-left: 15px;
    transition: ease-in 0.5s;
    transform: rotate(180deg);
}
