.friendsChat{
    width: 100%;
    padding: 20px 130px;
    font-size: 2rem;
}
.chatContainer{
    display: flex;
    justify-content: center;
    height: 80%;
    width: 60%;
    margin: 0px auto;
    border: 2px solid black;
    box-shadow: 0px 0px 5px black;
}
.chatSecondContainer{
    width: 100%;
}
.chatUserInfo{
    color: azure;
}
.chatUserInfos{
    background-color: navy;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.chatUserInfo p{
    text-align: center;
    margin: 0px 30px;
    font-size: 2rem;
}
.chatMessages{
    height: 82%;
    overflow-y: scroll;
    border-bottom: 3px solid black;
}
.user{
    float: right;
}
.leftUser{
    float:left
}
.chatMessages li{
    display: block;
}
.chatInput{
    position: sticky;
    display: flex;
    margin: auto;
    align-items: center;
    justify-content: center;
    width: 70%;
    overflow-y: hidden;
}
