.loginContainer{
    height: 100vh;
    width: 100%;
}
header{
    width: auto;
    text-align: center;
    margin-top: 30px;
    font-size: 3rem;
    color: blue;
}
.subtitle{
    text-align: center;
    color: darkblue;
    margin-top: -20px;
    font-size: 1.5rem;
}
.loginSecondContainer{
    margin: auto;
    text-align: center;
    width: 50%;
    border: 0.3px solid #ccc;
    padding: 30px;
    box-shadow: 0px 0px 16px #ccc;
}
.loginButtonContainer{
    width: 50%;
    margin: auto;
    margin-top: 20px;
}
.loginButton{
    align-items: center;
    margin-top: 2rem;
    color: aliceblue;
    padding: 5px;
    background-color: blue;
    font-size: 2rem;
}
.loginButton:hover{
    background-color: rgb(3, 3, 77);
    color: white;
    transition: ease-in 0.5s;
}

.loginFacebookButton p{
    padding: 2px;
    border-bottom: 2px solid rgb(51, 39, 226);
}
.loginGoogleButton p{
    padding: 2px;
    border-bottom: 2px solid rgb(236, 51, 128);
}
.loginPolicy{
    align-items:'center';
}
.loginPolicyText{
    font-weight:'bold';
    margin-top:10;
    text-align:'center';
}
.loginPolicyTerms{
    color: red;
}
.loginSignupButton{

}
.click{
    color: red;
}