.userProfileTheme{
    margin: 20px;
    color: navy;
    font-size: 2rem;
    font-weight: bolder;
}
.userProfileThemeInfo{
    display: flex;
    text-align: center;
}
.userProfileContainer{
    height: 80%;
    margin: auto;
    width: 60%;
    box-shadow: 0px 0px 5px black;
    border: 3px solid #ccc;
}
.goChat{
    position: absolute;
    top: 100px;
    right: 80px;
    background-color: navy;
    color: aliceblue;
    border-radius: 10px;
    padding: 6px 6px;
}